.JokeList{
    display: flex;
    height:80%;
    width:80%; 

}

.JokeList-sidebar{
    background: #9575cd;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 30%;
    text-align: center;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1),
    inset 0 0 25px #7e57c2;
    z-index:2;
}


.JokeList-title{
  font-size: 2rem;
  margin: 2rem;
  color: white;
  font-weight: 300;
  letter-spacing: 0.6rem;      
}


.JokeList-title span{
    font-weight: 700;
    letter-spacing: 0;
}

.JokeList-sidebar img{
  
    width: 50%;
     
}

 .JokeList-jokes{
     height:90%; 
     /* width:50% */
    background: white;
    align-self: center;
    width: 70%;
    overflow: scroll;

} 


.JokeList-spinner{
    color:darkslategray;
    text-align: center;
}